import { Injectable } from "@angular/core";
import * as CryptoJS from "crypto-js";
import { environment } from "./../../../environments/environment";

@Injectable({
  providedIn: "root"
})
export class CrypterService {
  constructor() {}

  encryptData(data, keyType) {
    try {
      let resp = CryptoJS.AES.encrypt(
        JSON.stringify(data),
        environment[keyType]
      ).toString();
      try {
        resp = JSON.parse(resp);
      } catch (e) {}
      return resp;
    } catch (e) {
      console.log(e);
    }
  }

  decryptData(data, keyType) {
    try {
      const crypter = this.Crypt(environment[keyType]);
      return JSON.parse(crypter.decrypt(JSON.stringify(data)));
    } catch (e) {
      console.log(e);
      return { status: "failed" };
    }
  }

  Crypt(passphrase) {
    const pass = passphrase;
    const CryptoJSAesJson: any = {
      parse: jsonStr => {
        const j: any = JSON.parse(jsonStr);
        const cipherParams = CryptoJS.lib.CipherParams.create({
          ciphertext: CryptoJS.enc.Base64.parse(j.ct)
        });
        if (j.iv) {
          cipherParams.iv = CryptoJS.enc.Hex.parse(j.iv);
        }
        if (j.s) {
          cipherParams.salt = CryptoJS.enc.Hex.parse(j.s);
        }
        return cipherParams;
      },
      stringify: cipherParams => {
        const j: any = {
          ct: cipherParams.ciphertext.toString(CryptoJS.enc.Base64)
        };
        if (cipherParams.iv) {
          j.iv = cipherParams.iv.toString();
        }
        if (cipherParams.salt) {
          j.s = cipherParams.salt.toString();
        }
        return JSON.stringify(j);
      }
    };

    return {
      decrypt: data => {
        return JSON.parse(
          CryptoJS.AES.decrypt(data, pass, {
            format: CryptoJSAesJson
          }).toString(CryptoJS.enc.Utf8)
        );
      },
      encrypt: data => {
        return CryptoJS.AES.encrypt(JSON.stringify(data), pass, {
          format: CryptoJSAesJson
        }).toString();
      }
    };
  }
}
