import { Injectable } from "@angular/core";
import { ConnectorService } from "../connecter/connector.service";

@Injectable({
  providedIn: "root"
})
export class ValidatorService {
  constructor(private conn: ConnectorService) {}

  private wordList(input, entities, expectation, user) {
    let model = {};
    return new Promise(function(resolve, reject) {
      try {
        if (expectation.val) {
          let entityValues = Object.keys(expectation.val);
          for (let entity of entities) {
            let flag = false;
            for (let value of entityValues) {
              if (
                entity.synonymGroup &&
                typeof entity.synonymGroup == "string" &&
                value &&
                typeof value == "string" &&
                entity.synonymGroup.trim() == value.trim()
              ) {
                flag = true;
              }
            }
            if (flag) {
              if (expectation.val[entity.synonymGroup]) {
                model["stage"] = expectation.val[entity.synonymGroup];
              }
              model["data"] = entity.synonymGroup;
              model["validated"] = true;
              return resolve(model);
            }
          }
        }
        model["data"] = input;
        model["validated"] = false;
        return resolve(model);
      } catch (e) {
        return reject(e);
      }
    });
  }

  private regex(inp, entities, expectation, user) {
    let model = {};
    return new Promise(function(resolve, reject) {
      try {
        if (expectation.val && expectation.val.trim()) {
          let reg = new RegExp(expectation.val.trim());
          if (expectation.val && inp.match(reg)) {
            model["data"] = inp.match(reg)[0];
            model["validated"] = true;
            return resolve(model);
          } else {
            model["validated"] = false;
            return resolve(model);
          }
        } else {
          model["validated"] = false;
          return resolve(model);
        }
      } catch (e) {
        // //console.log(e);
        return reject(e);
      }
    });
  }
  private post(input, entities, post, user) {
    let that = this;
    return new Promise(async (resolve, reject) => {
      try {
        let model = {};
        model["data"] = input;
        model["validated"] = true;
        model["stage"] = user.stages[user.tracker];
        return resolve(await that.conn.sendMiddlewareRequest(model).post());
      } catch (e) {
        return reject(e);
      }
    });
  }

  process(text, user, entities) {
    let validator = {
      wordList: this.wordList,
      regex: this.regex
    };
    let that = this;
    return new Promise(async function(resolve, reject) {
      try {
        let expectation;
        let post;
        if (
          user &&
          user.stages &&
          user.stages.length > user.tracker &&
          user.stages[user.tracker] &&
          user.stages[user.tracker].next &&
          user.stages[user.tracker].next.expectation
        ) {
          expectation = user.stages[user.tracker].next.expectation;
        } else if (
          user &&
          user.stages &&
          user.stages.length > user.tracker &&
          user.stages[user.tracker] &&
          user.stages[user.tracker].next &&
          user.stages[user.tracker].next.post &&
          user.stages[user.tracker].next.post.length > 0
        ) {
          post = user.stages[user.tracker].next.post[0];
        }

        if (expectation && expectation.type) {
          let runFunc = validator[expectation.type];
          resolve(await runFunc(text, entities, expectation, user));
        } else if (post && post.url) {
          let resp = await that.post(text, entities, post, user);
          resp["user"] = user;
          resolve(resp);
        } else {
          resolve({
            data: text,
            validated: true,
            user: user
          });
        }
      } catch (e) {
        return reject(e);
      }
    });
  }
}
