import { Injectable } from "@angular/core";
import * as io from "socket.io-client";
import { Observable, Subject } from "rxjs";
import * as Rx from "rxjs";
import { environment } from "src/environments/environment";
import { resolve } from "url";
import { CrypterService } from "../crypter/crypter.service";

@Injectable({
  providedIn: "root"
})
export class ConnectorService {
  public webViewObservable = new Subject<any>();
  public externalObservable = new Subject<any>();
  public timeoutObservable = new Subject<any>();
  public speechObservable = new Subject<any>();
  private voiceConnector: any;
  private middlewareConnector: any;
  private backendConnector: any;
  private webId;
  constructor(private crypt: CrypterService) {
    this.webId = this.IDGenerator(8);
    this.voiceConnector = io(environment.voiceSocketEndpoint.domain, {
      path: environment.voiceSocketEndpoint.path,
      transports: ["websocket"]
    });
    this.middlewareConnector = io(environment.middlewareSocketEndpoint.domain, {
      path: environment.middlewareSocketEndpoint.path,
      transports: ["websocket"]
    });
    this.backendConnector = io(environment.backendSocketEndpoint.domain, {
      path: environment.backendSocketEndpoint.path,
      transports: ["websocket"]
    });
    this.initListeners();
    this.middlewareConnector.on("disconnect", function() {});
  }

  public sendVoiceRequest(data: any) {
    let that = this;
    data.webId = this.webId;
    data.requestId = this.IDGenerator(20);
    return {
      textToSpeechStream: () => {
        return new Promise((resolve, reject) => {
          that.voiceConnector.on(
            "web-text-to-speech-" + data.webId + "-" + data.requestId,
            resp => {
              return resolve(that.crypt.decryptData(resp, "transitKey"));
            }
          );
          that.voiceConnector.emit(
            "web-text-to-speech",
            that.crypt.encryptData(data, "transitKey")
          );
        });
      },
      startSpeechToText: () => {
        return new Promise((resolve, reject) => {
          that.voiceConnector.emit(
            "web-speech-to-text-stop",
            that.crypt.encryptData(
              {
                webId: this.webId
              },
              "transitKey"
            )
          );
          return resolve();
        });
      },
      stopSpeechToText: () => {
        return new Promise((resolve, reject) => {
          that.voiceConnector.emit(
            "web-speech-to-text-stop",
            that.crypt.encryptData(
              {
                webId: this.webId
              },
              "transitKey"
            )
          );
          return resolve();
        });
      },
      sendSpeechData: () => {
        return new Promise((resolve, reject) => {
          that.voiceConnector.emit(
            "web-speech-to-text-binary-data",
            that.crypt.encryptData(data, "transitKey")
          );
          return resolve();
        });
      }
    };
  }
  public sendBackendRequest(data: any) {
    let that = this;
    data.webId = this.webId;
    data.requestId = this.IDGenerator(20);
    return {
      registerEvent: () => {
        return new Promise((resolve, reject) => {
          that.backendConnector.on(
            "web-event-register-" + data.webId + "-" + data.requestId,
            resp => {
              return resolve();
            }
          );
          that.backendConnector.emit(
            "web-event-register",
            that.crypt.encryptData(data, "transitKey")
          );
        });
      },
      registerError: () => {
        return new Promise((resolve, reject) => {
          that.backendConnector.on(
            "web-event-register-error-" + data.webId + "-" + data.requestId,
            resp => {
              return resolve();
            }
          );
          that.backendConnector.emit(
            "web-event-register-error",
            that.crypt.encryptData(data, "transitKey")
          );
        });
      }
    };
  }
  public sendMiddlewareRequest(data: any) {
    let that = this;
    data.webId = this.webId;
    data.requestId = this.IDGenerator(20);
    return {
      pre: () => {
        return new Promise((resolve, reject) => {
          that.middlewareConnector.on(
            "web-pre-" + data.webId + "-" + data.requestId,
            resp => {
              try {
                resp = JSON.parse(resp);
              } catch (e) {}
              return resolve(resp);
            }
          );
          that.middlewareConnector.emit("web-pre", data);
        });
      },
      post: () => {
        return new Promise((resolve, reject) => {
          that.middlewareConnector.on(
            "web-post-" + data.webId + "-" + data.requestId,
            resp => {
              try {
                resp = JSON.parse(resp);
              } catch (e) {}
              return resolve(resp);
            }
          );
          that.middlewareConnector.emit("web-post", data);
        });
      },
      save: () => {
        return new Promise((resolve, reject) => {
          that.middlewareConnector.on(
            "web-save-" + data.webId + "-" + data.requestId,
            resp => {
              try {
                resp = JSON.parse(resp);
              } catch (e) {}
              return resolve(resp);
            }
          );
          that.middlewareConnector.emit("web-save", data);
        });
      }
    };
  }
  private IDGenerator(length) {
    let timestamp = +new Date();
    let _getRandomInt = function(min, max) {
      return Math.floor(Math.random() * (max - min + 1)) + min;
    };
    let ts = timestamp.toString();
    let parts = ts.split("").reverse();
    let id = "";

    for (let i = 0; i < length; ++i) {
      let index = _getRandomInt(0, parts.length - 1);
      id += parts[index];
    }
    return id;
  }
  private initListeners() {
    this.middlewareConnector.on(
      "web-webview-" + this.webId.toString(),
      data => {
        this.webViewObservable.next(data);
      }
    );
    this.middlewareConnector.on(
      "web-external-" + this.webId.toString(),
      data => {
        this.externalObservable.next(data);
      }
    );
    this.middlewareConnector.on(
      "web-timeout-" + this.webId.toString(),
      data => {
        this.timeoutObservable.next(data);
      }
    );
    this.voiceConnector.on("speech-data", async function(data) {
      this.speechObservable.next(this.crypt.decryptData(data, "transitKey"));
    });
  }
}
