import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { CrypterService } from "../crypter/crypter.service";
import { environment } from "./../../../environments/environment";

@Injectable({
  providedIn: "root"
})
export class dataFetcherService {
  constructor(private http: HttpClient, private crypt: CrypterService) {}

  fetchData() {
    return new Promise((resolve, reject) => {
      this.http
        .get(environment.fetchUrl + environment.projectId)
        .subscribe(response => {
          let res = this.crypt.decryptData(response, "fetchKey");
          return resolve(res);
        });
    });
  }
}
