import { Injectable } from "@angular/core";
import { NluService } from "../nlu/nlu.service";
import { dataFetcherService } from "../dataFetcher/dataFetcher.service";
import { async } from "q";
import { ValidatorService } from "../validator/validator.service";
import { FlowTrackerService } from "../flowTracker/flow-tracker.service";
import Tokenizer from "sentence-tokenizer";
import { Subject } from "rxjs";
const sentTokenizer = new Tokenizer("webBot");
import { environment } from "./../../../environments/environment";
import { ConnectorService } from "../connecter/connector.service";
@Injectable({
  providedIn: "root"
})
export class EngineService {
  private tags: any = {};
  private user: any = {};
  private dataSet: any;
  public readyObservable = new Subject<any>();
  constructor(
    private nlu: NluService,
    private fetcher: dataFetcherService,
    private validator: ValidatorService,
    private tracker: FlowTrackerService,
    private conn: ConnectorService
  ) {
    let that = this;
    (async () => {
      that.dataSet = await fetcher.fetchData();
      that.readyObservable.next(await that.run(environment.initQuery));
    })();
  }
  //---------------------------------------------------- 
  getTags(){
      return this.tags;
  }
  // -----------------------------------------------------

  run(text) {
    let that = this;
    return new Promise(async (resolve, reject) => {
      let timestampstart = new Date().getTime();
      let nluProcessedModel = await that.nlu.process(that.dataSet, text);
      let validatedModel = await that.validator.process(
        text,
        that.user,
        nluProcessedModel["entities"]
      );
      that.user = validatedModel["user"];
      let prevStage = await that.saveDetails(
        text,
        nluProcessedModel,
        validatedModel
      );
      let flowManagerData = await that.tracker.process(
        that.dataSet,
        that.user,
        that.tags,
        {
          query: text,
          intents: nluProcessedModel["intents"],
          topIntents: nluProcessedModel["top"],
          validation: validatedModel
        }
      );
      // console.log(validatedModel);
      that.user = flowManagerData["user"];
      that.savePostDetails(
        text,
        nluProcessedModel,
        prevStage,
        flowManagerData["response"],
        validatedModel
      );
      let timestampend = new Date().getTime();
      that.conn
        .sendBackendRequest({
          channel: "web",
          webInformation: "",
          projectId: environment.projectId,
          input: {
            text: text,
            user: that.user,
            tags: that.tags
          },
          requestAssistance: that.tags.blockBot,
          apiTime: timestampend - timestampstart,
          output: flowManagerData["response"],
          nlu: nluProcessedModel,
          status: flowManagerData["status"],
          type: "process"
        })
        .registerEvent();
      return resolve(
        that.fixEmpty(
          await that.transform(
            that.replaceTagsFromStage(flowManagerData["response"])
          )
        )
      );
    });
  }

  private fixEmpty(data) {
    let botMessage = [];
    data["botMessage"].forEach(element => {
      if (element["value"]) {
        botMessage.push(element);
      }
    });
    data["botMessage"] = botMessage;
    return data;
  }
  private saveDetails(text, nluProcessedModel, validatedModel) {
    let that = this;
    return new Promise(async (resolve, reject) => {
      let prevStage = {};
      if (that.user.stages) {
        prevStage = that.user.stages[that.user.tracker];
      }
      that.user.previousOptions = undefined;
      that.user.previousQuery = undefined;
      let expectation;
      if (
        that.user &&
        that.user.stages &&
        that.user.stages.length > that.user.tracker &&
        that.user.stages[that.user.tracker] &&
        that.user.stages[that.user.tracker].next &&
        that.user.stages[that.user.tracker].next.expectation
      ) {
        expectation = that.user.stages[that.user.tracker].next.expectation;
      }
      if (expectation) {
        let saveResponse = await that.saveInformationInBackend(
          "pre",
          validatedModel,
          prevStage,
          {},
          nluProcessedModel,
          text
        );
        if (saveResponse["tags"]) {
          that.tags = saveResponse["tags"];
        }
      } else {
        that
          .saveInformationInBackend(
            "pre",
            validatedModel,
            prevStage,
            {},
            nluProcessedModel,
            text
          )
          .then(response => {
            if (validatedModel && validatedModel["tags"]) {
              that.tags = validatedModel["tags"];
            }
            if (
              validatedModel &&
              validatedModel["data"] &&
              prevStage &&
              prevStage["stage"]
            ) {
              that.tags[prevStage["stage"]] = validatedModel["data"];
            }
          })
          .catch(e => {
            // //console.log(e)
          });
        if (validatedModel && validatedModel["data"]) {
          that.tags[prevStage["stage"]] = validatedModel["data"];
        }
      }
      return resolve(prevStage);
    });
  }
  private savePostDetails(
    text,
    nluProcessedModel,
    prevStage,
    stageModel,
    validatedModel
  ) {
    let that = this;
    that
      .saveInformationInBackend(
        "post",
        validatedModel,
        prevStage,
        stageModel,
        nluProcessedModel,
        text
      )
      .then(reponse => {
        that.tags = reponse["tags"];
      })
      .catch(e => {});
  }

  private saveInformationInBackend(
    type,
    validatedModel,
    prevStage,
    stageModel,
    nluProcessedModel,
    text
  ) {
    let that = this;
    return new Promise(async (resolve, reject) => {
      let input = {
        type: type,
        validation: validatedModel,
        prevStage: prevStage,
        nlu: nluProcessedModel,
        text: text,
        stage: stageModel
      };
      that.conn.sendMiddlewareRequest(input).save();
    });
  }
  private replaceTagsFromStage(stage) {
    if (Array.isArray(stage.text)) {
      for (let index in stage.text) {
        stage.text[index] = this.replaceAllTags(stage.text[index]);
      }
    } else {
      stage.text = this.replaceAllTags(stage.text);
    }
    if (stage.type == "button" || stage.type == "quickReply") {
      for (let index in stage.next.data) {
        stage.next.data[index].data = this.replaceAllTags(
          stage.next.data[index].data
        );
        stage.next.data[index].text = this.replaceAllTags(
          stage.next.data[index].text
        );
      }
    }
    return stage;
  }
  private replaceAllTags(text) {
    let match = /\${[a-zA-Z0-9_]*}/g.exec(text);
    if (match) {
      do {
        // //console.log(match[0])
        let matchedTag = match[0].replace("${", "").replace("}", "");
        if (this.tags[matchedTag]) {
          text = text.replace(match[0], this.tags[matchedTag]);
        } else {
          text = text.replace(match[0], "");
        }
        match = /\${[a-zA-Z0-9_]*}/g.exec(text);
      } while (match);
    }
    return text;
  }
  private transform(response) {
    if (typeof response == "string") {
      response = JSON.parse(response);
    }
    function replaceTags(text) {
      let match = /\${[a-zA-Z0-9_]*}/g.exec(text);
      return text.replace(match, "");
    }
    function findMatch(str) {
      let match = /\${(image|file|audio|video)::[^(${|})]+}/g.exec(str);
      if (match && match.length > 0) {
        return match;
      } else {
        return undefined;
      }
    }
    function transformMediaOrText(text, i) {
      if (findMatch(text)) {
        let match = text
          .replace("${", "")
          .replace("}", "")
          .split("::");
        return {
          id: i,
          type: match[0],
          value: match[1]
        };
      } else {
        return {
          id: i,
          type: "text",
          value: replaceTags(text)
        };
      }
    }

    return new Promise((resolve, reject) => {
      try {
        if (Array.isArray(response.text) && response.text.length == 1) {
          response.text = response.text[0];
        }
        //extract media
        let tempStr = response.text;
        let match = findMatch(tempStr);
        let mediaFlag = false;
        let botMessage = [];
        if (typeof response.text === "string") {
          while (match) {
            response.text = response.text.replace(
              match[0],
              "\\n" + match[0] + "\\n"
            );
            tempStr = tempStr.replace(match[0], "");
            match = findMatch(tempStr);
            mediaFlag = true;
          }
          // new line
          let respArray = response.text.split("|break|");
          response.text = response.text.split("|break|").join("\\n");
          if (response.text && response.text.includes("\\n")) {
            response.text = response.text.split("\\n");
          } else if (response.text && response.text.length > 60 && !mediaFlag) {
            sentTokenizer.setEntry(response.text);
            response.text = sentTokenizer.getSentences();
          }
        }
        if (typeof response.text === "string") {
          botMessage.push(transformMediaOrText(response.text, 0));
        } else if (response.text instanceof Array) {
          let textArray = response.text;
          for (let i = 0; i < textArray.length; i++) {
            botMessage.push(transformMediaOrText(textArray[i], i));
          }
        }
        let options = [];
        let currentButtonContext = {};
        switch (response.type) {
          case "button":
            let sameButton = false;
            for (let i = 0; i < response.next.data.length; i++) {
              options.push({
                type: response.next.data[i].type,
                text: response.next.data[i].text,
                data: response.next.data[i].data
              });
              if (
                currentButtonContext[
                  response.next.data[i].text.toLowerCase().trim()
                ]
              ) {
                sameButton = true;
                currentButtonContext = {};
              }
              if (!sameButton) {
                currentButtonContext[
                  response.next.data[i].text.toLowerCase().trim()
                ] = response.next.data[i].data.toLowerCase().trim();
              }
            }
            resolve({
              botMessage: botMessage,
              answerType: "persist-option",
              options: options
            });
            break;
          case "quickReply":
            for (let i = 0; i < response.next.data.length; i++) {
              options.push({
                type: response.next.data[i].type,
                text: response.next.data[i].text,
                data: response.next.data[i].data
              });
            }
            resolve({
              botMessage: botMessage,
              answerType: "option",
              options: options
            });
            break;
          case "generic":
            resolve({
              botMessage: botMessage,
              answerType: "generic",
              options: response.next.data
            });
            break;
          default:
            resolve({
              botMessage: botMessage,
              answerType: "text"
            });
            break;
        }
      } catch (e) {
        // //console.log(e);
        return reject(e);
      }
    });
  }
}
